import React, { Component }  from 'react';
import style from '../../css/style.css'; 
import  logo  from '../../img/logoC.png';
import firebase from 'firebase';
import {connect} from 'react-redux';
import { 
    modificaNome,
    modificaEmail,
    modificaSenha,
    cadastraUsuario
} from '../../redux/actions';
import { Link, Redirect } from 'react-router-dom';
import { RotateSpinner } from 'react-spinners-kit'

class Cadastro extends Component {
    constructor(props){
        super(props);        
        this.state = {
            nome:'',
            email: '',
            senha: '',
            redirect:'',
            loading: false,    
        }
        this.noReload = this.noReload.bind(this);
        this.cadastrarUsuario = this.cadastrarUsuario.bind(this);
    }

    async cadastrarUsuario(){
        
        const {nome, email, senha, loading } = this.state;
        this.setState({ nome: this.props.nome });

        this.setState({ loading: true });
        setTimeout( () => {
            this.setState({ loading: false })
        }, 2000 )

        await this.props.cadastraUsuario({ nome, email, senha });
    }

    noReload(e){
        e.preventDefault(this);
    }

    render(){
        const { loading } = this.state; 
        return (
            <div class="base login">
                <form onSubmit={this.noReload}>
                    <table class="popUpLogin">                        
                        <tr>
                            <td>
                                <img class="logo" src={logo} />
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <h1>Cadastre-se</h1>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <p>Nome:</p>
                                <input class="campoLogon" onChange={(e) => {this.setState({nome:e.target.value})} }/>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <p>Endereço de E-mail:</p>
                                <input class="campoLogon" onChange={(e) => {this.setState({email:e.target.value})} }/>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <p>Senha:</p>
                                <input class="campoLogon" type="password" onChange={(e) => {this.setState({senha:e.target.value})} }/>
                            </td>
                        </tr>
                        <tr>
                            <td>                                
                                <input class="botaoAction" type="submit" value="Cadastrar" onClick={this.cadastrarUsuario} />
                                { 
                                    loading 
                                    ? <div  class="loading" ><RotateSpinner size={35} color="yellow" /></div>
                                    : <h5 class="erro">{ this.props.erroCadastro } </h5> 
                                }
                                { this.props.redirect == 'Login' 
                                ? <Redirect to="/CadastroSucesso"> { console.log('Redireciona p CadastroSucesso') }</Redirect>
                                : <Redirect to="/Cadastro"> { console.log('redireciona p Cadastro') }</Redirect> }
                            </td>
                        </tr>
                        <tr><td>
                            <Link to="/Login"> <h3>Volte ao Login </h3></Link>
                        </td></tr>
                    </table>
                
                </form>
            </div>
        );
    }
}


const mapStateToProps = state => (
    {
        nome : state.CalculosReducers.nome,
        email : state.CalculosReducers.email,
        senha : state.CalculosReducers.senha,
        erroCadastro: state.CalculosReducers.erroCadastro,
        redirect: state.CalculosReducers.redirect,
    }
)

export default connect(mapStateToProps, {
    modificaEmail,
    modificaNome,
    modificaSenha,
    cadastraUsuario
})(Cadastro); 