export const cadastraUsuario = ({ nome, email, senha }) => {
    return dispatch => {
        firebase.auth().createUserWithEmailAndPassword( email, senha )
        .then(user => { cadastroUsuarioSucesso(dispatch) })
        .catch(erro => cadastroUsuarioErro(erro, dispatch));
    }
}
export const CADASTRO_USUARIO_SUCESSO = 'CADASTRO_USUARIO_SUCESSO';
const cadastroUsuarioSucesso = (dispatch) => {
    dispatch ({
        type:'CADASTRO_USUARIO_SUCESSO',
    });
}
export const CADASTRO_USUARIO_ERRO = 'CADASTRO_USUARIO_ERRO';
const cadastroUsuarioErro = (erro, dispatch) => {
    let errorMessage = erro.message;
    if(errorMessage == 'The email address is badly formatted.'){
        errorMessage = 'O campo email está vazio ou mal formatado!'
    }
    if(errorMessage == 'The password must be 6 characters long or more.'){
        errorMessage = 'A senha deve ter 6 caracteres ou mais.'
    }
    if(errorMessage == 'There is no user record corresponding to this identifier. The user may have been deleted.'){
        errorMessage = 'Não há registro do email em nosso banco de dados. Verifique o email digitado ou registre-se!'
    }
    return dispatch ({
        type:'CADASTRO_USUARIO_ERRO',
        payload: errorMessage
    })
}
export const autenticaUsuario = ({inputEmail, inputSenha}) => {
    return dispatch => {
        firebase.auth().signInWithEmailAndPassword(inputEmail, inputSenha)
        .then(value => autenticaUsuarioSucesso(dispatch) )
        .catch(errorMessage => autenticaUsuarioErro(errorMessage, dispatch));        
    }
}
export const AUTENTICA_USUARIO_SUCESSO = 'AUTENTICA_USUARIO_SUCESSO';
const autenticaUsuarioSucesso = (dispatch) => {
    dispatch ({
        type:'AUTENTICA_USUARIO_SUCESSO'
    });
}
export const AUTENTICA_USUARIO_ERRO = 'AUTENTICA_USUARIO_ERRO';
const autenticaUsuarioErro = (erro, dispatch) => {
    let errorMessage = erro.message;
    if(errorMessage == 'The email address is badly formatted.'){
        errorMessage = 'Preencha o campo email corretamente!'
    }
    if(errorMessage == 'The password is invalid or the user does not have a password.'){
        errorMessage = 'Senha inválida!'
    }
    if(errorMessage == 'There is no user record corresponding to this identifier. The user may have been deleted.'){
        errorMessage = 'Não há registro do email em nosso banco de dados. Verifique o email digitado ou registre-se!'
    }
    dispatch ({
        type:'AUTENTICA_USUARIO_ERRO',
        payload: errorMessage
    });
}
