export const recuperaSenha = ({ inputEmail }) => {
    console.log( inputEmail )
    return dispatch => {
        firebase.auth().verifyPasswordResetCode(inputEmail)
        .then(value => recuperaSenhaSucesso(dispatch) )
        .catch(errorMessage => recuperaSenhaErro(errorMessage, dispatch));        
    }
}
